@use '../abstract/breakpoints' as *;
@use '../abstract/colors' as *;
@use "../abstract/fonts" as *;
@use '../abstract/functions' as *;
@use '../abstract/mixins' as *;


.hero{
    background: $clr-secondary;
    h1{
        font-family: $ff-bold;
        font-weight: 700;
    }

    &__contents{
        height: 100%;
        @include flex-cloumn;
        justify-content: center;
        color:$clr-white;
    }
    &__btns{
        display: flex;
        flex-wrap: wrap;
        gap:rem(16);
        padding-bottom: 4px;

        @include breakpoint-sm{
            gap:rem(24);
        }
        @include breakpoint-md{
            gap:rem(36);
        }
        @include breakpoint-lg{
            gap:rem(48);
        }
    }
}

.about{
    &__contents{
     
    }
}

// Department styles
.department{
    background: $clr-secondary;
}

// stats styles

.counters{
    background: $clr-secondary;

    p{
        font-size: rem(18);
        font-weight: 500;
        font-family: $ff-semiBold;
        @include breakpoint-md{
            font-size: rem(20);
        }
    }
    &__wrapper{
        @include flex-center;
        flex-direction: column;
    }
}
.count{
    color:$clr-white;
    font-family: $ff-extraBold;
    &::after{
        content: "+";
        margin-left: rem(4);
    }
}

// Reviews
.reviews{

    &__wrapper{
        p{
            padding-right: em(16);
            @include breakpoint-sm{
                padding-right: em(64);
            }
        }
    }
    &__figure{
        display: flex;
        align-items: center;
        gap:rem(32);
    }
    &__imgWrapper{
        width: 160px;
        height: 160px;
        @include border-radius(50%);
        border:3px solid $clr-primary;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            @include border-radius(50%);
        }
    }
}

.img-hover{
    position: relative;
    overflow: hidden;

    img{
       transition:transform 0.35s ease-in;
       object-fit: cover;
    }
    &:hover{
        img{
            transform:scale(1.065);
        }
    }
}
.wrapper{
    position: relative;
    overflow: hidden;
}
.img-reveal{
    position: absolute;
    top: 0;
    left: 0%;
    width: 100%;
    height: 100%;
    background: #1524F0;
    background: linear-gradient(to right, #1524F0 0%, #23538A 100%);
}